@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #003366;
  background: url(/static/media/bg.f981924e.jpg) top center;
  background-size: cover;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  color: #333333;
}

.wrapper {
  width: 1000px;
  max-width: 100%;
  margin: 0 auto;
  background: #ffffff;
  padding: 20px;
}

.column {
  font-weight: bold;
}

.data-table {
  border-spacing: 5px;
}

.singleForm,
.multiForm {
  margin-bottom: 20px;
}

/* The main area to make the fields side by side */
.fieldArea {
  /* width: 600px; */
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 250px);
  grid-gap: 20px;
  margin-bottom: 20px;
}

.inputField {
}

.inputField label {
  display: block;
  margin-bottom: 10px;
}

.inputField input {
  font-size: 1em;
  width: 100%;
  padding: 5px;
}

.submit {
  font-size: 1em;
}

.multiForm {
}

.multiForm textarea {
  width: 400px;
  height: 100px;
  display: block;
  margin-bottom: 20px;
  font-size: 1em;
  padding: 5px;
}

table {
  width: 100%;
  max-width: 100%;
}

table,
th,
td {
  border: 1px solid #ddd;
  border-collapse: collapse;
  padding: 10px;
}

th {
  text-align-last: left;
}

hr {
  width: 90%;
  border-color: #ddd;
  border-top: 0;
}

.credits {
  text-align: center;
  font-size: 0.9em;
}

@media only screen and (max-width: 600px) {
  .fieldArea {
    grid-template-columns: auto;
    grid-gap: 20px;
  }

  .inputField input {
    width: 100%;
  }

  .multiForm textarea {
    width: 100%;
  }
}

.displayTableArea {
}

.table_row {
  display: grid;
  grid-template-columns: repeat(6, auto);
}

.buttonSide {
  margin-bottom: 10px;
}

a.submit {
  background: green;
  color: #ffffff;
  border: 1px solid #ddd;
  padding: 5px 10px;
  cursor: pointer;
}

